@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Roboto", sans-serif;
}

.instagram-container {
  width: 1039px;
  height: 775px;
  border: 1px solid rgb(255, 219, 225);
  display: flex;
}
.profile-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 825px;
}

.sidebar {
  width: 215px;
  height: 100%;
  box-shadow: 2px 0px 20px 5px #f3f3f3;
}


.sidebar-item {
  display: flex;
  align-items: center;
  padding: 14px 5px;
  color: #9a9999;
  font-size: 32px;
}
.active-item {
  background-color: #f9f9f9;
  border-left: 2px solid #f73e60;
}

.item-icon {
  height: 36px;
}
.item-title {
  font-size: 18px;
}
.item-icon,
.item-title {
  margin-left: 20px;
}

.header {
  height: 65px;
  width: 824px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  box-shadow: 0px 1px 30px 1px #fff5f7;
}
.logo {
  width: 40px;
}

.search {
  position: relative;
}
input[type="search"] {
  border: none;
  padding: 7px 20px;
  font-size: 18px;
  background-color: #f4f4f4;
  width: 330px;
  border-radius: 20px;
}
.search-icon {
  position: absolute;
  top: 8px;
  right: 12px;
  font-size: 22px;
  color: #f73e60;
}

.person-info {
  display: flex;
  align-items: center;
}
.person-name-job-loc {
  display: flex;
  flex-direction: column;
  margin-left: 12px;
}
.profile-img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.person-name {
  font-size: 10px;
  font-weight: bold;
  color: #f73e60;
}
.person-job,
.person-loc {
  color: #dedede;
  font-size: 8px;
}

.stories {
  width: 100%;
  height: 150px;
  display: flex;
  align-items: center;
}

.add-new {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 55px;
}
.add-new .add-new-icon {
  background-color: #f73e60;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  font-size: 1.5rem;
  display: flex;

  justify-content: center;
  align-items: center;
  color: white;
}
.add-new p {
  font-size: 10px;
  margin-top: 2px;
  color: #b3b3b3;
}

.relevant-stories {
  display: flex;
  align-items: center;
}

.story {
  margin-left: 32px;
}
.story-images {
  position: relative;
  width: 118px;
  margin-left: 20px;
}
.story-images,
.story-tags {
  display: flex;
  align-items: center;
  font-size: 10px;
}
.story-tags {
  justify-content: space-between;
  margin-top: 5px;
}
.tag1 {
  color: #929292;
}
.tag2 {
  color: #b2b1b1;
  margin-right: 5px;
}
.person-img {
  width: 60px;
  height: 75px;
  border-radius: 50%;
  border: 3px solid white;
  box-sizing: content-box;
  background-color: white;
  position: absolute;
  left: -30px;
}
.tags-img {
  width: 118px;
  height: 75px;
}

.posts-container {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.post {
  width: 203px;
  height: 234px;
  box-shadow: 1px 1px 10px 0px #cccccc;
}

.post-body {
  width: 100%;
  height: 160px;
}
.post-body img {
  width: 100%;
  height: 100%;
}
.post-footer {
  width: 100%;
  height: 32px;
  display: flex;
}
.m-top {
  margin-top: 32px;
}

.post-header {
  width: 100%;
  height: 38px;
  display: flex;
  justify-content: space-between;
  padding: 5px;
}
.h-person-info {
  display: flex;
}
.h-person-name {
  font-size: 10px;
  margin-left: 8px;
  margin-top: 3px;
  color: rgb(136 136 138 / 78%);
}
.h-person-loc {
  font-size: 7px;
  margin-left: 8px;
  margin-top: 2px;
  color: #dbd5d5;
}
.h-person-img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 2px solid #f73e60;
}
.more {
  font-size: 25px;
  color: #f73e60;
  font-weight: 700;
}

.post-footer {
  display: flex;
  justify-content: space-between;
}
.post-reaction {
  display: flex;
  justify-content: space-around;
  width: 100px;
  align-items: center;
  margin-top: 10px;
}
.heart {
  color: #f73e60;
}
.post-save {
  margin-top: 10px;
  margin-right: 10px;
  color: #cccccc;
}
.send,
.message {
  color: #cccccc;
}

.posts {
  width: calc(100% - 150px);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

